var MAX, MIN, MinimaxAgent, ref;

ref = ['MAX', 'MIN'], MAX = ref[0], MIN = ref[1];

MinimaxAgent = (function() {
  function MinimaxAgent(depth) {
    this.depth = depth != null ? depth : Infinity;
    this.rootAgent = MAX;
  }

  MinimaxAgent.prototype.nextAction = function(state) {
    var _, bestAction, ref1;
    this.rootAgent = state.nextAgent();
    ref1 = this.minimax(state), _ = ref1[0], bestAction = ref1[1];
    return bestAction;
  };

  MinimaxAgent.prototype.minimax = function(state, α, β, ply) {
    var isTerminal;
    if (α == null) {
      α = -Infinity;
    }
    if (β == null) {
      β = +Infinity;
    }
    if (ply == null) {
      ply = 0;
    }
    isTerminal = state.isTerminal();
    if (!isTerminal && state.nextAgent() === this.rootAgent) {
      ++ply;
    }
    if (ply > this.depth || isTerminal) {
      return [state.utility(), null];
    } else if (state.nextAgent() === MAX) {
      return this.maxi(state, α, β, ply);
    } else {
      return this.mini(state, α, β, ply);
    }
  };

  MinimaxAgent.prototype.maxi = function(state, α, β, ply) {
    var _, a, action, i, len, nextValue, ref1, ref2, ref3, ref4, successor, v;
    ref1 = [-Infinity, null], v = ref1[0], a = ref1[1];
    ref2 = state.possibleActions();
    for (i = 0, len = ref2.length; i < len; i++) {
      action = ref2[i];
      successor = state.play(action);
      ref3 = this.minimax(successor, α, β, ply), nextValue = ref3[0], _ = ref3[1];
      if (nextValue > v) {
        ref4 = [nextValue, action], v = ref4[0], a = ref4[1];
      }
      if (v >= β) {
        return [v, a];
      }
      α = Math.max(α, v);
    }
    return [v, a];
  };

  MinimaxAgent.prototype.mini = function(state, α, β, ply) {
    var _, a, action, i, len, nextValue, ref1, ref2, ref3, ref4, successor, v;
    ref1 = [+Infinity, null], v = ref1[0], a = ref1[1];
    ref2 = state.possibleActions();
    for (i = 0, len = ref2.length; i < len; i++) {
      action = ref2[i];
      successor = state.play(action);
      ref3 = this.minimax(successor, α, β, ply), nextValue = ref3[0], _ = ref3[1];
      if (nextValue < v) {
        ref4 = [nextValue, action], v = ref4[0], a = ref4[1];
      }
      if (v <= α) {
        return [v, a];
      }
      β = Math.min(β, v);
    }
    return [v, a];
  };

  return MinimaxAgent;

})();

module.exports = {
  MAX: MAX,
  MIN: MIN,
  MinimaxAgent: MinimaxAgent
};
